@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
.outline {
    font-family: 'Roboto',serif,sans-serif,'Times New Roman', Times;
    background-image: url(/travel-website/static/media/wallpaper.1e096b0c.jpeg);
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    height: 120vh;
    justify-content: center;
    align-items: center;
}
.title {
    text-align: center;
    padding: 1rem;
    color: rgb(207, 32, 61);
    font-size: 30px;
    font-weight: bold;
}
.wallpaper2 {
    background-image: url(/travel-website/static/media/wallpaper2.6f10b5d2.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh;
}

@media only screen and (max-width:600px){
    .outline{
        background-size: contain;
        height: 31.7vh;
        color: white;
    }
    .title{
        color: rgb(207, 32, 61);
        font-size: 17px;
    }
    .wallpaper2 {
        background-size: contain;
        height: 40vh;
    }
}
#about {
    text-align: center;
    color: rgb(77, 75, 76);
    padding: 10px;
}
.services {
    display: flex;
    padding: 50px;
    flex-wrap: nowrap;
    justify-content: space-evenly;
}
.serv {
    color: rgb(255, 136, 185);
    padding: 10px;
}
#contactBody {
    text-align: center;
    color: white;
    background: #5a5a59;
    padding: 5px;
}
.contacts {
    display: flex;
    padding: 50px;
    flex-wrap: nowrap;
    justify-content: space-evenly;
}
.cont {
    color: white;
    padding: 5px;
}
@media only screen and (max-width:600px){
    .contacts {
        display: flex;
    }
    .cont {
        flex-direction: row;
    }
}

* {
    margin: 0;
    padding: 0;
    scroll-behavior: smooth;
}
a {
    text-decoration: none;
}

